  max-width: 1100px;
  gap: 20px;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 36rem) {
  .collections__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 75rem) {
  .collections__grid {
    gap: 25px;
  }
}
.collections__item {
  height: 150px;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 15px;
  overflow: hidden;
}
.collections__item:nth-child(1) {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}
.collections__item:nth-child(2) {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}
.collections__item:nth-child(3) {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}
.collections__item:nth-child(4) {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}
.collections__item .itemTitle {
  color: var(--whitesmoke);
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1rem;
}
@media (min-width: 75rem) {
  .collections__item .itemTitle {
    font-size: 1.125rem;
  }
}
.collections__item .overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: var(--collItems-overlay);
}

.gallery {
  gap: 26px;
  padding: 70px 40px 0 40px;
  background-color: var(--black);
  gap: 40px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 75rem) {
  .gallery {
    gap: 50px;
    padding: 115px 40px 0 40px;
  }
}
.gallery__title {
  color: var(--white);
  font-size: 26px;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  line-height: 40px;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 400;
  font-style: normal;
}
@media (min-width: 36rem) {
  .gallery__title {
    font-size: 28px;
  }
}
@media (min-width: 48rem) {
  .gallery__title {
    font-size: 30px;
  }
}
@media (min-width: 75rem) {
  .gallery__title {
    font-size: 34px;
  }
}
@media (min-width: 87.5rem) {
  .gallery__title {
    font-size: 36px;
  }
}
.gallery__pictures {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  gap: 20px;
}
.gallery__pictures .box {
  position: relative;
  height: 270px;
  flex-grow: 1;
  border-radius: 20px;
  overflow: hidden;
}
@media (min-width: 48rem) {
  .gallery__pictures .box {
    height: 300px;
  }
}
@media (min-width: 62rem) {
  .gallery__pictures .box:hover .iconBar {
    visibility: visible;
  }
}
.gallery__pictures img {
  border-radius: 20px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 1s;
  cursor: pointer;
}
.gallery__pictures img:hover {
  transform: scale(1.2);
}

.footerUp {
  width: 100%;
  padding: 90px 0 40px;
  text-align: center;
  font-size: 0.875rem;
  background-color: transparent;
  color: var(--white);
